import { FreeMode } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import { useEffect } from "react";
import { useState } from "react";
import "swiper/css";
import "swiper/css/free-mode";
import useWindowDimensions from "./GetDimensions";
function TeletypeCarousel() {
  const { height, width } = useWindowDimensions();
  const [teletype, setTeletype] = useState("teletype_block");
  const [swiper, setSwiper] = useState(null);
  const [swiper_mount, setSwiperMount] = useState(null);
  useEffect(() => {
    if (width < 410) {
      setTeletype("teletype_block_small");
    } else if (width >= 410) {
      setTeletype("teletype_block");
    }
    // if (swiper != null) {
    //   swiper.translateTo(69, 200, false, false);
    // }
  }, [width]);
  useEffect(() => {
    setTimeout(() => {
      if (swiper) {
        swiper.translateTo(-100, 200, false, false);
      }
    }, 1000)
  });
  return (
    <div id="teletype_carousel_div">
      <div></div>
      <Swiper
        modules={[FreeMode]}
        freeMode={true}
        slidesPerView={3}
        height={255}
        onSwiper={(s) => {
          setSwiper(s);
          setSwiperMount(true);
          console.log(s);
        }}
      >
        <SwiperSlide>
          <div className={teletype}>
            <div>
              <p>    </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className={teletype}>
            <div>
              <p>    </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className={teletype}>
            <div>
              <p>    </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className={teletype}>
            <div>
              <p>    </p>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className={teletype}>
            <div>
              <p>    </p>
            </div>
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
  );
}
export default TeletypeCarousel;
